<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-toolbar>
        <ec-header title="基地单元集中交易计划量维护" icon="onetomany" color-code="003" />
      </ec-panel-toolbar>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
            <ec-query-item label="工/商业">
              <ec-select
                showSearch
                allowClear
                placeholder=""
                v-model="queryItems.orgTypeEk"
                @select="onQuery"
              >
                <a-select-option v-for="b in orgTypeListOptions" :key="b.id">
                  {{ b.label }}
                </a-select-option>
              </ec-select>
            </ec-query-item>
            <ec-query-item label="省级单位">
              <org-picker
                title="选择省级单位"
                :lazy="false"
                :search="false"
                v-model="queryItems.orgCode"
                :limitLevel="2"
              />
            </ec-query-item>
            <ec-query-item label="烟叶类型">
              <leaf-select v-model="queryItems.leafTypeCode" useType="TYPE" />
            </ec-query-item>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <!-- 表格工具栏 -->
        <ec-panel-item>
          <ec-panel-item-left>
            <a-button type="primary" :disabled="false" @click="handlerCreateBtn">生成</a-button>
            <a-button :disabled="false" @click="handlerPush">发送</a-button>
            <a-button @click="handlerOpen">设置办理依据</a-button>
            <a-button>导出</a-button>
          </ec-panel-item-left>
          <ec-panel-item-right>
            <div class="unit-box">
              <span>业务年度：{{ busiYear }}年</span>
              <div class="select">
                重量单位：
                <enum-select
                  v-model="weightUnit"
                  :enumTypeCodes="['WEIGHT_UNIT']"
                  @select="onWeightUnitChange"
                />
              </div>
            </div>
          </ec-panel-item-right>
        </ec-panel-item>
        <ec-panel-body>
          <ec-table
            ref="tableRef"
            :loading="loading"
            height="100%"
            :auto="false"
            show-overflow
            :data="tableData"
            show-footer
            :footer-method="footerMethod"
          >
            <ec-table-col type="seq" title="序号" width="50" />
            <ec-table-col field="orgName" title="省级单位" align="center" />
            <ec-table-col field="leafTypeCode" title="烟叶类型" align="center">
              <template #default="{ row }">
                <div>
                  {{
                    row.leafTypeCode
                      | getNameBy(leafTypeList, { key: 'leafTypeCode', label: 'leafTypeName' })
                  }}
                </div>
              </template>
            </ec-table-col>
            <ec-table-col field="sumWeight" title="计划量" align="center">
              <template #default="{ row }">
                <div>{{ row.sumWeight | amountFormat(weightUnit) }}</div>
              </template>
            </ec-table-col>
            <ec-table-col field="transStates" title="发送状态" align="center">
              <template #default="{ row }">{{
                row.transStates === '0' ? '未发送' : row.transStates === '1' ? '已发送' : ''
              }}</template>
            </ec-table-col>
            <ec-table-col title="操作" width="190" align="right">
              <template #default="{ row }">
                <a-button type="link" @click="onEdit(row)">编辑</a-button>
                <a-button type="link" @click="onDetail(row)">详细</a-button>
              </template>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>

      <edit-modal
        ref="editModalRef"
        :leafTypeList="leafTypeList"
        :industryOrgList="industryOrgList"
        :replaceFields="replaceFields"
        @saved="onQuery"
      />
      <remark-modal ref="remarkModalRef" />
    </ec-container-body>
  </ec-container>
</template>

<script>
import EditModal from './EditModal.vue';
import RemarkModal from './RemarkModal.vue';
import { EnumSelect, LeafSelect, OrgPicker } from '@euler-material/ecm-ui';
import _ from 'lodash';

export default {
  name: 'BaseCentreMaintain',
  components: {
    EditModal,
    EnumSelect,
    LeafSelect,
    OrgPicker,
    RemarkModal,
  },
  data() {
    return {
      loading: false,
      leafTypeList: [], // 烟叶类型
      inquirevisible: false,
      weightUnit: '500000',
      orgTypeListOptions: [
        { id: 'TOBACCO', label: '商业企业' },
        { id: 'INDUSTRY', label: '工业企业' },
      ],
      queryItems: {
        orgTypeEk: 'TOBACCO',
      },
      tableData: [],
      industryOrgList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
    };
  },
  computed: {
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
    bizCode() {
      return this.$store.getters.getAppConfig?.bizCode;
    },
    orgTypeEk() {
      return this.$store.getters.getAppConfig?.orgTypeEk;
    },
  },
  created() {
    this.handleQueryLeafTypeList();
    this.queryItems.busiYear = this.busiYear;
    this.onQuery();
    this.handleQueryIndustryOrgList();
  },
  methods: {
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 查询工业单位
    async handleQueryIndustryOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'INDUSTRY',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.industryOrgList = res.rows || [];
    },
    // 查询
    onQuery() {
      const params = {
        ...this.queryItems,
      };
      this.loading = true;
      this.$apis.apm.baseCentreMaintain
        .query(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 重置
    onReset() {
      this.queryItems = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
      };
    },
    // 编辑
    onEdit(data) {
      this.$refs.editModalRef.show(data);
    },
    // 合计的数据
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      count /= Number(this.weightUnit);
      return count.toFixed(4);
    },
    // 底部合计行
    footerMethod({ columns, data }) {
      console.log('columns', columns);
      console.log('data', data);
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return '合计';
          }
          if (['sumWeight'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },
    // 生成
    async handlerCreateBtn() {
      const params = {
        busiYear: this.busiYear,
      };
      const { result } = await this.$apis.apm.baseCentreMaintain.beforeGenerate(params);

      const content = result
        ? () => (
            <div style="color:red;">
              本年度已经生成基地单元计划量数据，如果继续生成，会将已经生成的数据删除，是否继续？
            </div>
          )
        : () => (
            <div style="color:red;">是否确定生成？，确认请点击'确认'按钮，取消点击'取消'按钮</div>
          );

      this.$confirm({
        title: '是否执行该操作?',
        content,
        onOk: () => {
          $ec.showLoading();
          this.$apis.apm.baseCentreMaintain.generate(params).then((res) => {
            if (res.result) {
              this.$message.success('生成成功');
            } else {
              this.$message.error('生成失败');
            }
            $ec.hideLoading();
            this.onQuery();
          });
        },
      });
    },
    // 发送
    handlerPush() {
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">是否发送数据？，确认请点击'确认'按钮，取消点击'取消'按钮。</div>
        ),
        onOk: () => {
          $ec.showLoading();
          this.$apis.apm.baseCentreMaintain.send({ busiYear: this.busiYear }).then(() => {
            $ec.hideLoading();
            this.$message.success('发送成功');
            this.onQuery();
          });
        },
      });
    },
    // 设置办理依据
    handlerOpen() {
      this.$refs.remarkModalRef.show();
    },
    // 重量单位变化，重新计算合计
    onWeightUnitChange() {
      this.tableData = _.cloneDeep(this.tableData);
    },
    onDetail(data) {
      this.$refs.editModalRef.show(data, true);
    },
  },
};
</script>
<style lang="less" scoped>
.table-bar-icon-btn {
  width: 32px;

  > i {
    margin-left: -4px;
  }
}
.textBold {
  font-weight: bold;
  font-size: 18px;
}
/deep/.ant-modal-body {
  padding: 6px 24px;
}
/deep/.ec-query-item.org .ec-query-item__control {
  transform: translateY(10px);
}
.label-room-style {
  min-width: 68px;
  line-height: 30px;
}
.w100p {
  width: 100%;
}
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
