<template>
  <ec-modal v-model="visibile" title="设置办理依据" size="mini" :is-confirm="true">
    <ec-form :model="formData" :rules="formRules" ref="formRef">
      <a-row type="flex" :gutter="12">
        <a-col :span="24">
          <a-form-model-item label="办理依据" prop="remark">
            <a-textarea v-model="formData.remark" :rows="5" />
          </a-form-model-item>
        </a-col>
      </a-row>
    </ec-form>
    <template v-slot:footer>
      <a-button type="primary" @click="onConfirm">确定</a-button>
      <a-button @click="onClose">关闭</a-button>
    </template>
  </ec-modal>
</template>

<script>
export default {
  name: 'RemarkModal',
  data() {
    return {
      visibile: false,
      formData: {},
      formRules: {
        remark: { required: true, message: '请输入办理依据', trigger: 'blur' },
      },
    };
  },
  watch: {
    visibile: {
      handler(newVal) {
        if (!newVal) {
          this.formData = {};
        }
      },
    },
  },
  computed: {
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
  },
  methods: {
    show() {
      this.visibile = true;
    },
    onConfirm() {
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.formData.busiYear = this.busiYear;
          $ec.showLoading();
          this.$apis.apm.baseCentreMaintain.remark(this.formData).then(() => {
            $ec.hideLoading();
            this.$message.success('设置成功！');
            this.onClose();
          });
        } else {
          this.$message.error('验证不通过');
        }
      });
    },
    onClose() {
      this.$refs.formRef.resetFields();
      this.visibile = false;
    },
  },
};
</script>
