<template>
  <div>
    <ec-drawer title="编辑" :visible="visible" size="large" @close="onCancel" :is-confirm="true">
      <ec-container height="100%">
        <ec-container-body>
          <ec-panel :auto="false">
            <ec-panel-toolbar>
              <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
                <ec-query-item label="基地单元名称">
                  <a-input v-model.trim="query.baseCellName" placeholder="请输入" />
                </ec-query-item>
                <ec-query-item label="市公司">
                  <org-picker
                    :limitLevel="3"
                    :params="{ orgTypeEk: 'TOBACCO' }"
                    title="选择市公司"
                    v-model="query.cityOrgCode"
                  />
                </ec-query-item>
                <ec-query-item label="对口工业企业">
                  <select-picker
                    v-model.trim="query.linkInduOrgCode"
                    placeholder="请选择对口工业企业"
                    :lazy="false"
                    :search="false"
                    :fetchData="onFetchIndustryOrgList"
                  />
                </ec-query-item>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>
            <ec-panel-body>
              <ec-v-layout :auto="false">
                <!-- 表格工具栏 -->
                <ec-v-item height="auto">
                  <ec-panel-toolbar>
                    <ec-panel-item-left v-if="!isDetail">
                      <a-button type="primary" @click="onSave">保存</a-button>
                      <a-button type="default" @click="onCancel">关闭</a-button>
                    </ec-panel-item-left>
                    <ec-panel-item-right>
                      <div class="unit-box">
                        <span>业务年度：{{ busiYear }}年</span>
                        <div class="select">
                          重量单位：
                          <enum-select
                            v-model="weightUnit"
                            :enumTypeCodes="['WEIGHT_UNIT']"
                            @select="computedWeigthHandler"
                          />
                        </div>
                      </div>
                    </ec-panel-item-right>
                  </ec-panel-toolbar>
                </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="tableRef"
                    :loading="loading"
                    show-overflow
                    height="100%"
                    :auto="false"
                    :data="tableData"
                    :edit-config="{ trigger: 'click', mode: 'row' }"
                  >
                    <ec-table-col type="seq" title="序号" width="50" />
                    <ec-table-col
                      field="provOrgName"
                      title="省公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="cityOrgName"
                      title="市公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="linkInduProvOrgName"
                      title="省级工业公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="linkInduOrgName"
                      title="对口工业企业"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="baseCellCode"
                      title="基地单元编码"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="baseCellName"
                      title="基地单元名称"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="leafTypeCode"
                      title="烟叶类型"
                      align="center"
                      min-width="100"
                    >
                      <template #default="{ row }">
                        <div>
                          {{
                            row.leafTypeCode
                              | getNameBy(leafTypeList, {
                                key: 'leafTypeCode',
                                label: 'leafTypeName',
                              })
                          }}
                        </div>
                      </template>
                    </ec-table-col>
                    <template v-if="isDetail">
                      <ec-table-col
                        min-width="150"
                        field="buyWeightTemp"
                        title="收购量"
                        align="center"
                      />
                      <ec-table-col
                        field="apprWeightTemp"
                        title="计划量"
                        align="center"
                        min-width="150"
                      />
                    </template>
                    <template v-else>
                      <ec-table-col
                        min-width="150"
                        field="buyWeightTemp"
                        title="收购量"
                        align="center"
                        :edit-render="{}"
                      >
                        <template #edit="{ row }">
                          <a-input-number v-model="row.buyWeightTemp" />
                        </template>
                      </ec-table-col>
                      <ec-table-col
                        field="apprWeightTemp"
                        title="计划量"
                        align="center"
                        min-width="150"
                        :edit-render="{ name: 'input', immediate: true }"
                      >
                        <template #edit="{ row }">
                          <a-input-number v-model="row.apprWeightTemp" />
                        </template>
                      </ec-table-col>
                    </template>
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
          </ec-panel>
        </ec-container-body>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { OrgPicker, EnumSelect, SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'EditModal',
  components: {
    OrgPicker,
    EnumSelect,
    SelectPicker,
  },
  props: {
    leafTypeList: {
      type: Array,
      default: () => [],
    },
    industryOrgList: {
      type: Array,
      default: () => [],
    },
    replaceFields: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
    orgTypeEk() {
      return this.$store.getters.getAppConfig?.orgTypeEk;
    },
  },
  data() {
    return {
      loading: false,
      visible: false,
      query: {},
      tableData: [],
      businesOrgCodeTemp: undefined,
      // 当前重量单位
      weightUnit: '500000',
      // 主页面编辑项
      dataTemp: {},
      // 详情
      isDetail: false,
    };
  },
  methods: {
    show(data, isDetail) {
      this.visible = true;
      this.dataTemp = data;
      this.isDetail = isDetail;
      this.query = {
        busiYear: this.busiYear,
        orgTypeEk: this.orgTypeEk,
        provOrgCode: data.provOrgCode,
        leafTypeCode: data.leafTypeCode,
      };

      this.onQuery();
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 查询
    onQuery() {
      if (!this.query.provOrgCode) return;
      const params = {
        ...this.query,
      };
      this.loading = true;
      this.$apis.apm.baseCentreMaintain
        .editQuery(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
          this.computedWeigthHandler();
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 根据单位计算重量
    computedWeigthHandler() {
      this.tableData = this.tableData.map((item) => {
        const newItem = item;
        newItem.buyWeightTemp = Number(newItem.buyWeight) / Number(this.weightUnit);
        newItem.apprWeightTemp = Number(newItem.apprWeight) / Number(this.weightUnit);
        return newItem;
      });
    },
    // 重置
    onReset() {
      this.query = {
        busiYear: this.busiYear,
        orgTypeEk: this.orgTypeEk,
        provOrgCode: this.dataTemp.provOrgCode,
        leafTypeCode: this.dataTemp.leafTypeCode,
      };
      this.onQuery();
    },
    // 取消
    onCancel() {
      this.visible = false;
    },
    // 保存
    onSave() {
      const params = {
        busiYear: this.busiYear,
        leafTypeCode: this.dataTemp.leafTypeCode,
        pcTBmUnitApprQtyDTOList: this.tableData.map((item) => {
          const newItem = item;
          newItem.buyWeight = Number(newItem.buyWeightTemp) * Number(this.weightUnit);
          newItem.apprWeight = Number(newItem.buyWeightTemp) * Number(this.weightUnit);
          return newItem;
        }),
      };
      $ec.showLoading();
      this.$apis.apm.baseCentreMaintain.save(params).then(() => {
        $ec.hideLoading();
        this.$message.error('保存成功');
        this.$emit('saved');
        this.onReset();
      });
    },
  },
};
</script>

<style lang="less" scoped>
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
